import React, {Component} from 'react';


const TabsContent = () => {
    return(
        <div className="content">
            {this.props.tabs.map((tab) => {
                let classActive = tab.active ? "is-active" : "";
                return(
                    <div className={"tabs__content " + classActive} style={tab.style}></div>
                )
            })}
        </div>
    );
}

export default TabsContent;



